/* Canvas size */
#app {
  height: 100%;
  width: 100%;
}
.container-fluid {
  padding-left: 0px;
  padding-right: 0px;
}
.row {
  margin-left: 0px;
  margin-right: 0px;
}
.size {
  height: 100%;
  width: 100%;
}
.margin {
  margin: 0px;
}
.padding {
  padding-left: 0px;
  padding-right: 0px;
}

/* Lottie + textura + slider */
.lottie {
  z-index: -100;
  width: 100%;
  margin: 0px;
  padding: 0px;
  position: absolute;
}
.textura {
  width: 100%;
  z-index: 999;
  position: absolute;
  opacity: 0.65;
}
.sliderdiv {
  height: 100%;
  width: 100%;
  z-index: 99999;
  position: absolute;
  padding: 50px;
}

.paperscene {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 9999;
}
canvas[resize] {
  width: 100%;
  height: 100%;
}
#myCanvas {
  height: 100%;
  width: 100%;
  background-color: transparent !important;
}
.konva {
  width: 100%;
  height: 100%;
  z-index: 99999;
  position: absolute;
}

/* Buttons */
.buttoncontainer {
  height: 100%;
  width: 100%;
  z-index: 99999;
  position: absolute;
}
.yellowbutton {
  background: url(button.svg);
  height: 100px;
  width: 100px;
  margin-top: 150%;
  margin-left: 41%;
}
